/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { Card } from 'react-bootstrap';
import { useMemo } from 'react';
import { distanceInWordsToNow } from 'date-fns';
import koLocale from 'date-fns/locale/ko';

interface Props {
  link: string;
  title: string;
  authorProfile: string;
  authorName: string;
  content: string;
  pubDate: string;
}
const BlogFeedCard = ({
  link,
  title,
  authorProfile,
  authorName,
  content,
  pubDate
}: Props) => {
  const ellipsedContent = useMemo(() => {
    return `${content.substring(0, 100)}...`;
  }, []);
  const publishedDate = useMemo(() => {
    return distanceInWordsToNow(pubDate, {
      addSuffix: true,
      locale: koLocale,
    })
  }, []);

  return (
    <a href={link} target="_blank" rel="noopener noreferrer">
      <Card className="contents-card">
        <Card.Body>
          <Card.Title>{title}</Card.Title>
          <Card.Subtitle className="text-muted">
            <img src={authorProfile} alt={authorName}/>
            {authorName}
          </Card.Subtitle>
          <Card.Text>{ellipsedContent}</Card.Text>
          <p css={css`
            text-align: right;
            font-size: .6rem;
            margin: 0;
          `}>
            {publishedDate}
          </p>
        </Card.Body>
      </Card>
    </a>
  );
}

export default BlogFeedCard;